<template>
  <div id="app" :style="`background: #${settings.backgroundColor.data}`">
    <SolveHistory :timeData="times" />
    
    <div>
      <Scrambler ref="scrambler" />
      <timerDisplay @solve="newTime" />
      <Avarages :timeData="times" />
    </div>
    
    <div>
      <Options />
    </div>
  </div>
</template>

<script>
// components
import SolveHistory from "@/components/SolveHistory.vue";
import Scrambler from "@/components/Scrambler.vue";
import TimerDisplay from '@/components/TimerDisplay.vue';
import Avarages from "@/components/Avarages.vue";
import Options from "@/components/Options.vue";

export default {
  name: 'App',

  mounted() {
    this.times = localStorage.getItem('solves')?.split(',') ?? [];
  },

  data() {
    return {
      settings: {
        backgroundColor: {
          type: 'color',
          data: '273746'
        }
      },

      times: []
    }
  },

  components: {
    SolveHistory,
    Scrambler,
    TimerDisplay,
    Avarages,
    Options
  },

  methods: {
    newTime(time) {
      this.times.push(time);
      localStorage.setItem('solves', this.times);
      this.$refs.scrambler.rescramble();
    }
  }
}
</script>

