<template>
  <div id="solveHistory">
      <table>
          <thead>
              <tr>
                  <th width="50" class="gray">#</th>
                  <th width="300">Time</th>
              </tr>
              <tr>&nbsp;</tr>
          </thead>

          <tbody>
              <tr v-for="(time, index) in [...timeData].reverse()" :key="index">
                  <th class="gray">{{ timeData.length - index }}</th>
                  <th>{{ formatTime(time) }}</th>
              </tr>
          </tbody>
      </table>

      <div v-if="timeData.length === 0" class="gray center">
          No<br />solves<br />found
      </div>
  </div>
</template>

<script>
export default {
    props: {
        timeData: {
            type: Array,
            default: () => []
        }
    },

    methods: {
        formatTime(numberOfMs) {
            const msPart = (Math.floor(numberOfMs / 10) % 100).toString().padStart(2, '0');
            const secPart = (Math.floor(numberOfMs / 1000) % 60).toString().padStart(2, '0');
            const minPart = (Math.floor(numberOfMs / 1000 / 60) % 60).toString().padStart(2, '0');
            return `${minPart == 0 ? '' : minPart + ':'}${secPart}.${msPart}`
        }
    }
}
</script>

<style scoped lang="scss">
table {
    padding-top: 20px;
}

.gray {
    color: #6c7a8c;
}

.center {
    text-align: center;
}

#solveHistory {
    height: 100vh;
    overflow-y: auto;
}
</style>