<template>
  <div id="scrambler">
    <span v-if="notation === 'normal'">
        {{ scramble }}
    </span>

    <span v-else-if="notation === 'cushan'">
        <img :src="`http://cushan.io/api?width=800&height=150&bgCol=00000000&alg=${formatScramble(scramble)}`" class="scramble" />
    </span>

    <img class="rescramble" src="@/assets/rescramble.png" alt="rescramble" @click="rescramble()" />
  </div>
</template>

<script>
import scrambler from 'scramble-generator';

export default {
    props: {
        notation: {
            type: String,
            default: "normal"
        }
    },

    data() {
        return {
            scramble: scrambler(),
            scrambler: scrambler,
        }
    },

    methods: {
        formatScramble(scramble) {
            const parts = scramble.split(/ /g);
            let acc = "";
            let accIndex = 0;
            const joinedParts = [];

            for (const part of parts) {
                if (acc != "") {
                    acc += " ";
                }

                acc += part;
                accIndex += 1;

                if (accIndex === 3) {
                    joinedParts.push(acc);
                    acc = "";
                    accIndex = 0;
                }
            }

            if (acc != "")
                joinedParts.push(acc);
            
            return joinedParts.join(',');
        },

        rescramble() {
            console.log("rescrambling")
            this.scramble = scrambler()
            console.log("donerino")
        }
    }
}
</script>

