<template>
  <div id="timer-display" :class="{ phase2: ready, phase1: waiting }">
    <span v-if="solving !== true && lastSolveTime === undefined">
      00.00
    </span>
    <span v-else-if="solving !== true && lastSolveTime !== undefined">
      {{ formatTime(lastSolveTime) }}
    </span>
    <span v-else>
      {{ formatTime(currentTimeMs) }}
    </span>
  </div>
</template>

<script>
export default {
  data() {
    return {
      ready: false,
      waiting: false,
      solving: false,
      touchStart: 0,
      readyEvent: null,
      startedTime: 0,
      currentTimeMs: 0,
      lastSolveTime: undefined
    }
  },

  methods: {
    startTimer() {
      this.startedTime = Date.now();
      this.solving = true;

      setInterval(() => {
        this.currentTimeMs = Date.now() - this.startedTime;
      });
    },

    formatTime(numberOfMs) {
      const msPart = (Math.floor(numberOfMs / 10) % 100).toString().padStart(2, '0');
      const secPart = (Math.floor(numberOfMs / 1000) % 60).toString().padStart(2, '0');
      const minPart = (Math.floor(numberOfMs / 1000 / 60) % 60).toString().padStart(2, '0');
      return `${minPart == 0 ? '' : minPart + ':'}${secPart}.${msPart}`
    }
  },

  mounted() {
    document.addEventListener("keydown", event => { 
      if (event.code === "Space" && this.solving) {
        console.log(`ms that the solve took: ${Date.now() - this.startedTime}`)
        this.solving = false;
        this.lastSolveTime = Date.now() - this.startedTime;
        this.$emit('solve', this.lastSolveTime);
        return;
      }

      if (event.code !== "Space" || this.waiting === true)
        return;

      this.touchStart = Date.now();
      this.waiting = true;

      this.readyEvent = setTimeout(() => {
        this.ready = true;
      }, 1000);
    });

    document.addEventListener("keyup", event => { 
      this.waiting = false;

      if (this.ready)
        this.startTimer()
      else
        clearTimeout(this.readyEvent)

      this.ready = false;
    });
  }
}
</script>

<style scoped lang="scss">
#timer-display {
    height: calc(100vh - 200px);
    font-size: 80px;
    display: flex;
    justify-content: center;
    align-items: center;

    &.phase1 {
      color: goldenrod;
    }

    &.phase2 {
      color: lightgreen;
    }
}
</style>