<template>
  <div id="avarages">
    <span class="avg-item"><span class="gray">pb: </span>{{ timeSlot1 }}</span>
    <span class="avg-item"><span class="gray">mo3: </span>{{ timeSlot2 }}</span>
    <span class="avg-item"><span class="gray">ao5: </span>/</span>
    <span class="avg-item"><span class="gray">ao12: </span>/</span>
  </div>
</template>

<script>
export default {
    props: {
        timeData: {
            type: Array,
            default: () => []
        }
    },

    computed: {
        timeSlot1() {
            if (this.timeData.length === 0)
                return '/';
            return this.formatTime(Math.min(...this.timeData))
        },

        timeSlot2() {
            if (this.timeData.length < 3)
                return '/';

            return this.formatTime(Math.floor(this.timeData.slice(-3).reduce((a, b) => Number(a) + Number(b), 0) / 3));
        }
    },

    methods: {
        formatTime(numberOfMs) {
            const msPart = (Math.floor(numberOfMs / 10) % 100).toString().padStart(2, '0');
            const secPart = (Math.floor(numberOfMs / 1000) % 60).toString().padStart(2, '0');
            const minPart = (Math.floor(numberOfMs / 1000 / 60) % 60).toString().padStart(2, '0');
            return `${minPart == 0 ? '' : minPart + ':'}${secPart}.${msPart}`
        }
    }
}
</script>

<style scoped lang="scss">
#avarages {
  text-align: center;
  font-size: 18px;
}

.gray {
  color: #6c7a8c;
}

.avg-item {
  margin-right: 25px;
}
</style>